import Vuex from 'vuex'
import Vue from 'vue'
import mblog from '../data/blogs'
import mfmovie from '../data/movie'

Vue.use(Vuex);

const actions = {
    get_blogs(content) {
        content.state.blogs = mblog.blogs.sort((a, b) => b['data'] - a['data'])
    },
    get_movie(content) {
        content.state.movie = mfmovie.movie
    },
    get_blogs_selected(content, categoriesList) {
        const categoriesSelected = categoriesList.map(m => {
            if(m.isActived) {
                return m.category
            }
        }).filter(res => res!=undefined)
        content.state.blogsSelected = content.state.blogs.map(m => {
            if(categoriesSelected.includes(m.categories)) {
                return m
            }
        }).filter(res => res!=undefined)
    }
}

const mutations= {

}

const state = {
    blogs: [],
    blogsSelected: [],
    movie: []
}

const getters = {
    blogsCategories(state) {
        const ori_categories = state.blogs.map(b => b.categories)
        return [...new Set(ori_categories)].map(m => {
            return {
                category: m,
                isActived: false
            }
        })
    }
}

export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})