<template>
  <div class="blogshow">
    <mavon-editor
      class="markdown"
      v-model="test"
      :subfield="settingMd.subfield"
      :defaultOpen="settingMd.defaultOpen"
      :toolbarsFlag="settingMd.toolbarsFlag"
      :boxShadow="settingMd.boxShadow"
      :previewBackground="settingMd.previewBackground"
    />
  </div>
</template>

<script>
import requestApi from '../api/requestApi'

export default {
  name: "Bolgshow",
  data() {
    return {
      settingMd: {
        subfield: false, // 单双栏模式
        defaultOpen: "preview", // 默认展示
        toolbarsFlag: false,
        boxShadow: false,
        previewBackground: "#fff",
        navigation: false, // 默认展示目录
        toolbars: {},
      },
      test: '',
      md: this.$route.params.md
    };
  },
  mounted() {
    requestApi.fetch(`blogMd/${this.md}.md`).then((res) => {
      this.test = res.data;
      // console.log(this.md)
    });
  },
};
</script>

<style lang="less">
.blogshow {
  padding-top: 100px;

  .markdown-body {
    border: 1px solid #fff;
    // border-radius: 0;
    // margin-bottom: 100px;
    // padding: 20px 50px;

    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 20px 0 15px;
      font-family: "Lato", "PingFang SC", "Microsoft YaHei", sans-serif;
      // line-height: 1.5;
      // font-weight: blod;
      color: #555;
      background: #fff;
      border-bottom: #fff;
    }

    p {
      margin-bottom: 25px;
      font-family: "Lato", "PingFang SC", "Microsoft YaHei", sans-serif;
      color: #555;
      font-size: 14px;
      line-height: 2;
    }
  }
}
</style>