<template>
    <div class="archive-list">
        <div class="md-categories">
            <div :class="{'md-category': true, 'md-category-actived': category.isActived}" v-for="category in mdCategories" :key="category.category">
                <span @click="category.isActived = !category.isActived">{{category.category}}</span>
            </div>
        </div>
        <div class="md-list">
            <div class="md" v-for="(md, index) in mdList" :key="index" @click="toBlogShow(md.data, md.id)">
                {{md.data + ' -- ' + md.title}}
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Archive',
    data() {
        return {
            mdList: [],
            mdCategories: []
        }
    },
    methods: {
        toBlogShow(mdData,mdId) {
            this.$router.push('/blog/' + mdData + '-' + mdId)
        }
    },
    mounted() {
        this.$store.dispatch('get_blogs');
        this.mdList = this.$store.state.blogs
        this.mdCategories = this.$store.getters.blogsCategories
    },
    beforeUpdate() {
        this.$store.dispatch('get_blogs_selected', this.mdCategories);
        this.mdList = this.$store.state.blogsSelected
    }
}
</script>

<style lang="less">
.archive-list {
    padding-top: 100px;
    width: 365px;
    margin: 0 auto;
    padding-left: 5px;
    .md-categories {
        .md-category {
            margin: 1px;
            padding: 5px 10px;
            display: inline-block;
            border: 2px solid rgba(34, 34, 34, 0.2);
            border-radius: 10px;
            &:hover {
                cursor: pointer;
                border: 2px solid rgba(34, 34, 34, 0.7);
            }
        }
    }
    .md-list {
        padding-top: 10px;
        .md {
            margin: 33px 0;
        }
    }
}

.md-category-actived {
    border: 2px solid rgba(34, 34, 34, 0.7) !important;
}

@media screen and (max-width: 365px) {
    .archive-list {
        width: 100%;
    }
}

/* Float */
.hvr-float {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.hvr-float:hover, .hvr-float:focus, .hvr-float:active {
  -webkit-transform: translateY(-8px);
  transform: translateY(-8px);
}
</style>